.brandlogo__img {
  max-width: 400px;
  max-height: 500px;
  width: 100%;
  height: 100%;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

@media (max-width: 767px) {
  .brandlogo {
    position: absolute !important;
    margin: auto;
    left: 0;
    top: 20%;
    z-index: 0;
  }

  .brandlogo__img {
    position: relative;
    left: 15%;
    top: 30%;
    width: 50%;
    height: 100%;
    max-width: 400px;
    max-height: 500px;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    opacity: 0.4;
    z-index: 1;
  }

  .title__info {
    z-index: 2;
  }
}

i {
  color: #777;
  font-size: 24px;
  opacity: 0.7;
}
a i:hover {
  color: #08fdd8;
  opacity: 1;
}

a i:active {
  color: #08fdd8;
  opacity: 1;
}

@media (max-width: 800px) {
  nav .align-items-center {
    -webkit-justify-content: start !important;
            justify-content: start !important;
  }

  nav i {
    font-size: 15px;
  }

  div.nav-end-links {
    justify-self: start;
    height: 100%;
  }
}

hr {
  width: 40px;
  background: #08fdd8;
}

td {
  position: relative;
  padding-left: 30px !important;
}

td::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  margin-left: 10px !important;
  width: 15px;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.25);
}

@media (max-width: 1225px) {
  td::before {
    display: none;
  }

  td {
    padding-left: 10px !important;
  }
}

@media (max-width: 770px) {
  .App-logo {
    display: none;
  }

  h1 {
    font-size: 40px !important;
  }

  p {
    font-size: 12px !important;
  }
}

.card_ {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: rgba(24, 24, 24, 0.7);
  max-width: 25%;
  max-height: 400px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.card_ a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.card__img {
  position: absolute;
  width: 100%;
  min-height: 100%;
  transition: 0.5s;
  box-shadow: -20px 20px 10px rgba(0, 0, 0, 0.2);
  border: 6px solid black;
}

.card--transform {
  -webkit-transform: rotate(-30deg) skew(25deg) scale(1.2);
          transform: rotate(-30deg) skew(25deg) scale(1.2);
  transition: 0.5s;
}

.card_:hover .card__img:nth-child(1) {
  -webkit-transform: translate(20px, -20px);
          transform: translate(20px, -20px);
  opacity: 0.4;
}
.card_:hover .card__img:nth-child(2) {
  -webkit-transform: translate(40px, -40px);
          transform: translate(40px, -40px);
  opacity: 0.6;
}
.card_:hover .card__img:nth-child(3) {
  -webkit-transform: translate(60px, -60px);
          transform: translate(60px, -60px);
  opacity: 0.8;
}
.card_:hover .card__img:nth-child(4) {
  -webkit-transform: translate(80px, -80px);
          transform: translate(80px, -80px);
  opacity: 1;
}

@media (max-width: 1000px) {
  .card_ {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background: rgba(24, 24, 24, 0.7);
    max-width: 100%;
    max-height: 350px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }

  .card__img {
    position: absolute;
    width: 100%;
    min-height: 100%;
    transition: 0.5s;
    box-shadow: -20px 20px 10px rgba(0, 0, 0, 0.2);
    border: 6px solid black;
  }
}

